/* base css */
.parent { 
  background-color: rgb(75, 80, 83);
  padding: 1rem 
}
.child {
  text-align: center;
  white-space: nowrap;
  padding: 1rem 
}
.parent_bin_menu {
  text-align: center;
  white-space: nowrap;
  padding: 1rem 
}
.grid-parent_bin_menu {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 
}
.grid-parent {
  display: grid;
  grid-template-columns: calc(100%/6) calc(100%/6) calc(100%/6) calc(100%/6) calc(100%/6) calc(100%/6) 
}
.parent_boxes_internal {
  background: #bbbaba;
  padding: 1rem 
}
.grid-parent_boxes_internal {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  background-color: rgb(164,172,176);
}
.parent_boxes {
  background: #808080;
  padding: 1rem 
}
.grid-parent_boxes_top {
  display: grid;
  grid-template-columns: 7% 15% auto 15% 7% 
}

.grid-parent_boxes_bottom {
    display: grid;
    grid-template-columns: 7% auto 7% 
  }

