body {
    margin: 0;
    background: #343434;
    font-family: 'Open Sans', sans-serif;
  }
  .pen-title {
    position: absolute;
    top: 50%;
    left: 0;
    right: 0;
    -webkit-transform: translate(0, -50%);
    transform: translate(0, -50%);
    text-align: center;
    color: white;
    font-size: 24px;
  }
  .pen-title .miniature {
    width: 100px;
    height: 130px;
    padding-top: 5px;
    background: transparent;
    margin: auto;
    text-align: center;
  }
  .pen-title .miniature .mini-menu {
    width: 66%;
    margin: auto;
    height: 5px;
    background: #fff;
    margin-top: 5px;
  }
  .pen-title .miniature .mini-menu:nth-of-type(4) {
    margin-bottom: 10px;
  }
  .pen-title .miniature .miniblock {
    width: 30px;
    height: 30px;
    background: tomato;
    display: inline-block;
  }
  .pen-title p {
    font-size: 12px;
  }
  .clickhere {
    float: left;
    margin-right: 60px;
    margin-top: 10px;
    color: white;
    font-size: 12px;
  }
  .menubtn {
    cursor: pointer;
    width: 40px;
    height: 40px;
    left: 0;
    top: 10px;
    position: relative;
  }
  .menubtn span {
    display: block;
    width: 30px;
    height: 3px;
    position: relative;
    background: #fff;
    transition: all 300ms ease;
  }
  .menubtn span::before {
    content: '';
    width: 30px;
    height: 3px;
    position: relative;
    top: 8px;
    background: #fff;
    transition: all 100ms ease;
  }
  .menubtn span::after {
    content: '';
    width: 30px;
    height: 3px;
    position: absolute;
    top: 16px;
    background: #fff;
    transition: all 300ms ease;
  }
  .menubtn.opened span {
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
    top: 7px;
  }
  .menubtn.opened span::before {
    opacity: 0;
  }
  .menubtn.opened span::after {
    -webkit-transform: rotate(-90deg);
    transform: rotate(-90deg);
    top: 0px;
  }
  .navmenu {
    width: 50vw;
    padding-top: 10px;
    padding-bottom: 10px;
    background: #fff;
    text-align: center;
    position: absolute;
    left: 10px;
    top: 25px;
    box-shadow: 0 0 10px 2px rgba(0, 0, 0, 0.15);
    z-index: 10;
    visibility: hidden;
    opacity: 0;
    transition: all 300ms ease;
  }
  .navmenu.opened {
    visibility: visible;
    opacity: 1;
  }
  .navmenu::before {
    content: '';
    position: absolute;
    top: -5px;
    left: 7px;
    width: 15px;
    height: 15px;
    background: #fff;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
  }
  .navmenu ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }
  .navmenu ul.text-list {
    text-align: left;
    width: 90%;
    margin: auto;
    border-top: 1px solid #eee;
    border-bottom: 1px solid #eee;
    padding-top: 10px;
    padding-bottom: 10px;
  }
  .navmenu ul.text-list li a {
    text-decoration: none;
    padding-left: 5px;
    padding-right: 5px;
    color: #343434;
    font-weight: 600;
    display: block;
    line-height: 27px;
    transition: all 200ms ease;
  }
  .navmenu ul.text-list li a:hover {
    color: tomato;
  }
  .navmenu ul.thumbs-list {
    margin: 10px auto;
    padding: 0;
    width: 90%;
  }
  .navmenu ul.thumbs-list::after {
    display: table;
    clear: both;
    content: '';
  }
  .navmenu ul.thumbs-list li {
    display: block;
    float: left;
    height: 50px;
    width: 50px;
    background: steelBlue;
    margin: 5px;
    transition: all 300ms ease;
  }
  .navmenu ul.thumbs-list li a {
    color: rgba(0, 0, 0, 0.9);
    text-align: center;
  }
  .navmenu ul.thumbs-list li a i {
    line-height: 98px;
    font-size: 40px;
  }
  .navmenu ul.thumbs-list li:nth-of-type(2) {
    background: tomato;
  }
  .navmenu ul.thumbs-list li:nth-of-type(3) {
    background: tomato;
  }
  .navmenu ul.thumbs-list li:nth-of-type(4) {
    background: #2ABB9B;
  }
  .navmenu ul.thumbs-list li:nth-of-type(6) {
    background: tomato;
  }
  .navmenu ul.thumbs-list li:hover {
    opacity: .8;
  }
  
